'use client';
import { useState, useEffect } from 'react';
import axios from 'axios'; //# axios„Çí‰ΩøÁî®„Åó„Å¶API„Å®ÈÄö‰ø°

// API„ÅÆ„Éô„Éº„ÇπURLÔºàÁí∞Â¢ÉÂ§âÊï∞„Åã„ÇâÂèñÂæóÔºâ
const API_BASE = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8010';

export default function StudioPage() {
  const [questions, setQuestions] = useState([
    "ÊúÄËøë„ÄÅ‰Ωï„Åã„Å´ÁüõÁõæ„ÇíÊÑü„Åò„Åü„Åì„Å®„ÅØ„ÅÇ„Çä„Åæ„Åô„ÅãÔºü",
    "„Åù„ÅÆÁüõÁõæ„ÇíËß£Ê±∫„Åô„Çã„Åü„ÇÅ„Å´„ÄÅ„Å©„Çì„Å™Ë°åÂãï„Çí„Å®„Çä„Åæ„Åó„Åü„ÅãÔºü",
    "Âä¥ÂÉç„ÅÆ‰æ°ÂÄ§„Å´„Å§„ÅÑ„Å¶„ÄÅ„ÅÇ„Å™„Åü„ÅÆËÄÉ„Åà„ÇíÊïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ"
  ]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [projectId] = useState(1); // ÂÆüÈöõ„ÅØÈÅ∏Êäû„Åï„Çå„ÅüID
  const [taskId] = useState(101); // ÂÆüÈöõ„ÅØ„Éû„Çπ„Çø„Å´Âü∫„Å•„ÅÑ„ÅüID

  // üí° ÂèéÈå≤ÈñãÂßãÊôÇ„ÅÆ„Çø„Ç§„Éû„ÉºÂßãÂãï
  const startQuestionTimer = async (index: number) => {
    try {
      await axios.post(`${API_BASE}/projects/${projectId}/tasks/${taskId}/start`);
      console.log(`Question ${index + 1} started`);
    } catch (e) {
      console.error("Timer start failed", e);
    }
  };

  const handleNext = async () => {
    // 1. ÁèæÂú®„ÅÆË≥™Âïè„ÅÆ„Çø„Ç§„Éû„Éº„ÇíÂÅúÊ≠¢
    try {
      await axios.post(`${API_BASE}/projects/${projectId}/tasks/${taskId}/stop`);
    } catch (e) {
      console.error("Timer stop failed", e);
    }

    // 2. Ê¨°„ÅÆË≥™Âïè„Å∏ÈÄ≤„ÇÄ
    if (currentIndex < questions.length - 1) {
      const nextIndex = currentIndex + 1;
      setCurrentIndex(nextIndex);
      // 3. Ê¨°„ÅÆË≥™Âïè„ÅÆ„Çø„Ç§„Éû„Éº„ÇíÈñãÂßã
      startQuestionTimer(nextIndex);
    } else {
      alert("„Åô„Åπ„Å¶„ÅÆË≥™Âïè„ÅåÁµÇ‰∫Ü„Åó„Åæ„Åó„ÅüÔºÅ„ÅäÁñ≤„ÇåÊßò„Åß„Åó„Åü„ÄÇ");
    }
  };

  return (
    <div className="flex">
      {/* „É°„Ç§„É≥„Ç≥„É≥„ÉÜ„É≥„ÉÑÔºàSidebar„ÅÆÂπÖÂàÜ„ÄÅÂ∑¶„Å´‰ΩôÁôΩ„ÇíÊåÅ„Åü„Åõ„ÇãÔºâ */}
      <div className="ml-64 p-10 w-full min-h-screen bg-slate-50 flex flex-col items-center justify-center">
        <div className="max-w-3xl w-full bg-white p-16 rounded-[40px] shadow-2xl border border-slate-100 text-center">
          <div className="text-emerald-500 font-black tracking-widest mb-6 uppercase">
            Step {currentIndex + 1} / {questions.length}
          </div>
          
          <h2 className="text-4xl font-black text-slate-800 mb-12 leading-tight">
            {questions[currentIndex]}
          </h2>

          <button 
            onClick={handleNext}
            className="w-full bg-slate-900 text-white py-6 rounded-2xl font-bold text-xl hover:bg-emerald-600 transition-all transform hover:scale-[1.02] active:scale-[0.98] shadow-xl"
          >
            {currentIndex === questions.length - 1 ? 'ÂèéÈå≤„ÇíÂÆå‰∫Ü„Åô„Çã' : 'ÂõûÁ≠îÂÆå‰∫Ü„ÄÅÊ¨°„ÅÆË≥™Âïè„Å∏'}
          </button>
        </div>
      </div>
    </div>
  );
}